export default {
	data() {
		return {
			imgs: [],
			tabIndex: 0,
			pageSize: 0,
			loadingText: '加载更多...',
			tabBars: [{
				name: '抖音',
				id: 'dou'
			}, {
				name: '朋友圈',
				id: 'friends'
			}, {
				name: '关注',
				id: 'guanzhu'
			}]
		}
	},
	onLoad: function(options) {
		setTimeout(function() {
			console.log('start pulldown');
		}, 1000);
		uni.startPullDownRefresh();
	},
	// 下拉方法
	onPullDownRefresh() {
		console.log('下拉刷新');
		this.pageSize = 0;
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
		this.loadingText = "加载更多"
	},
	// xia划方法
	onReachBottom: function() {
		console.log("下划")
		this.pageSize++;
		this.loadingText = "没有更多了"
	},
	methods: {
		getImgList(index) {
			this.imgs = [];
			switch (index) {
				case "0":
					for (var i = 0; i < 5; i++) {
						this.imgs.push("../../../static/cdnImg/task/douying/" + i + ".png")
					}
					break;
				case "1":
					for (var i = 0; i < 5; i++) {
						this.imgs.push("../../../static/cdnImg/task/friends/" + i + ".png")
					}
					break
				default:
					for (var i = 0; i < 5; i++) {
						this.imgs.push("../../../static/cdnImg/task/guanzhu/" + i + ".png")
					}
					break;
			}
		},
		// 切换触发
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.tabIndex = index
			this.getImgList(index)
			console.log(index)
		}
	},
	mounted() {
		this.getImgList(0);
	}
}
